/* Signup.module.css - scoped styles for Signup page */
.signupContainer {
    position: relative;
    width: 400px;
    background: #fff;
    box-shadow: 0 15px 50px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    margin: 120px auto 0 auto;
    padding: 40px 20px;
    max-width: 1200px;
}

.signupForm {
    width: 100%;
}

.signupTitle {
    color: #2c3e50;
    margin-bottom: 20px;
    text-align: center;
}

.inputGroup {
    margin-bottom: 15px;
}

.signupInput {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 16px;
}

.signupButton {
    width: 100%;
    padding: 12px;
    background-color: #034f1a;
    color: white;
    border: 2px solid #034f1a;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: 0.3s;
}

.signupButton:hover {
    background-color: #e1e1e1;
    color: #131413;
}

.googleBtn {
    background: #fff;
    border: 1px solid #ddd;
    color: #555;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    width: 100%;
}

.googleBtn img {
    width: 20px;
    margin-right: 10px;
}

.toggleForm {
    text-align: center;
    margin-top: 15px;
}

.termsSection {
    margin: 15px 0;
    display: flex;
    align-items: center;
    font-size: 1rem;
}

.termsSection input {
    width: auto;
    margin-right: 10px;
}

.termsSection a {
    color: #27ae60;
    text-decoration: none;
}

.termsSection a:hover {
    text-decoration: underline;
}

.errorMessage {
    color: #e74c3c;
    font-size: 0.9em;
    margin-top: 5px;
}
